<template>
    <div>
        <div class="kd-page-title">
            核对信息
        </div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info flex-col">
            <img
                :src="printImgUrls[0]"
                alt=""
            >
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="确认"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import audioObj from '@/assets/media/household/12.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IdCardProcessingPersonInfo',
    computed: {
        ...mapState('common', {
            printImgUrls: 'printImgUrls'
        })
    },
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('idCardProcessing', ['setResult']),
        preStep () { // 上一步
            this.changeStep('up');
        },
        nextStep () {
            this.changeStep('down');
        }
    },
    mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
    },
    beforeDestroy () {
        this.audioObj.destroy();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        text-align: center;

        img {
            height: 100%;
            width: auto;
        }
    }

</style>
