<template>
    <div>
        <div class="kd-page-title">领取人信息</div>
        <div class="kd-page-content-wrapper wrap-info">
            <kd-full-input
                class="inputClass"
                placeholder="请输入代领人的姓名"
                :watch-data="getIdentifyName"
            />
            <kd-full-input
                :ime-type="3"
                class="inputClass"
                placeholder="请输入代领人的身份证号码"
                :watch-data="getIdentifyNum"
                :limit-number="18"
            />
            <div class="checkTips hide">{{errorMsg}}</div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!!errorMsg || !partyName || !certNumber"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { REGULAR_MATCH } from '@/constant/constant';

export default {
    name: 'IdCardProcessingHandleType',
    data () {
        return {
            partyName: '',
            certNumber: '',
            errorMsg: ''
        };
    },
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        }),
        ...mapState('idCardProcessing', {
            placeOfCollection: 'placeOfCollection',
            phoneNumber: 'phoneNumber'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('idCardProcessing', ['setAcceptanceNumber']),
        getIdentifyName (data) {
            this.partyName = data;
            this.errorMsg = REGULAR_MATCH.name.test(this.partyName) ? '' : '姓名格式不正确';
        },
        getIdentifyNum (data) {
            this.certNumber = data;
            this.errorMsg = REGULAR_MATCH.idNumber.test(this.certNumber) ? '' : '身份证号格式不正确';
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            if (!this.errorMsg) {
                this.nextFunction(this.partyName, this.certNumber, this.placeOfCollection, this.phoneNumber);
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .checkTips {
        width: 60%;
        height: 40px;
        line-height: 40px;
        color: red;
    }

    .inputClass {
        display: inline-block;
        width: 60% !important;
        font-size: 26px;
        color: #636F93;
        margin: 10px 0;
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
</style>
