<template>
    <div>
        <identify-card-page
            v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
            :disable-read-card="true"
        />

        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'checkProcess')"
            :handle-business="checkQualifications"
        />

        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast
            v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"
            query-id-photo="queryIdPhoto"
        />
        <process
            :type="8"
            v-if="currentStep === steps.findIndex(step=>step.key === 'jumpProcess')"
            waitText="正在校验中，请稍后......"
            :handle-business="jumpProcess"
        />

        <phone-upload v-if="currentStep === steps.findIndex(step=>step.key === 'phoneUpload')" :onlyNeedPhone="true" :nextFunction="phoneUpload"/>

        <handle-type
            v-if="currentStep === steps.findIndex(step=>step.key === 'handleType')"
            :selfCollectionAddress="selfCollectionAddress"
            :nextFunction="getAcceptanceNumber"
        />
        <receiving-people
            v-if="currentStep === steps.findIndex(step=>step.key === 'receivingPeople')"
            :nextFunction="getOtherAcceptanceNumber"
        />

        <collect-finger-print
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'handCollectFirst')"
            :nextFunction="fingerUploadFirst"
            :historyFingerInfo="leftFingerInfo"
        />
        <collect-finger-print
            :type="3"
            v-if="currentStep === steps.findIndex(step=>step.key === 'handCollectSecond')"
            :nextFunction="fingerUploadSecond"
            :historyFingerInfo="rightFingerInfo"
        />

        <photo-tip-second v-if="currentStep === steps.findIndex(step=>step.key === 'photoTip')" :nextFunction="photoNext"/>
        <collect-complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'collectComplete')"
            :nextFunction="uploadPhotoResult"
        />

        <process
            :type="5"
            v-if="currentStep === steps.findIndex(step=>step.key === 'excelProcess')"
            waitText="正在处理，请稍后......"
            :handle-business="excelProcess"
        />

		<pay-query
				v-if="currentStep === steps.findIndex(step=>step.key === 'payQuery')"
				:acceptanceNumber="acceptanceNumber"
				:selfCollectionAddress="selfCollectionAddress"
				:nextFunction="resultProcess"
				:returnPrint="excelProcess"
		/>

        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>

        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="result"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import photoTipSecond from '@/view/pageComponents/photo-tip-second';
import collectComplete from './components/collect-complete';
import payQuery from './components/pay-query';
import handleType from './components/handle-type';
import personInfo from './components/person-info';
import receivingPeople from './components/receiving-people';
import complete from '@/components/complete';
import { checkQualifications, singleReflexHandleSavePhoto, submissionAccreditation, submitFingerprintData, updateCardComplete } from '@/service/human/identify-card';
import { handlePhotoRequest } from '@/utils/axiosHandle';
import { ID_NUMBER_CHANGE_YINCHUAN } from '@/constant/stepsOption';
import phoneUpload from '@/view/pageComponents/phone-upload';
import { fingerTranslationYinchuan, uploadFingerTranslationYinchuan } from '@/utils/dataTranlation';
import collectFingerPrint from '@/view/pageComponents/collect-finger-print';
import { addPrefix, cancelPrefix, urlGetQrEncode } from '@/utils/tools';
import { produceModelReq, toPrint } from '@/service/common';
import { getPrintPrefix } from '@/utils';

export default {
    name: 'IdCardProcessingIndex', // 身份证-期满换证
    components: { identifyCardPage, faceYitu, contrast, process, collectFingerPrint, photoTipSecond, collectComplete, payQuery, complete, handleType, phoneUpload, personInfo, receivingPeople },
    data () {
        return {
            changeCardCompleteRes: {},
            queryIdPhoto: '',
            phoneNumber: null, // 手机号码
            placeOfCollection: '', // 领取方式
            selfCollectionAddress: '',
            machineAddress: '',
            originFingerInfo: {}, // 原始指纹
            leftFingerInfo: { pos: '16' },
            rightFingerInfo: { pos: '13' },
            newLeftFingerInfo: {},
            newRightFingerInfo: {},
            uploadFingerInfo: {}, // 更新后的指纹
            acceptanceNumber: null, // 受理号
            result: {},
            jumpStep: ''
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera',
            businessTypeCode: 'businessTypeCode',
            singleReflexHandlePhoto: 'singleReflexHandlePhoto',
            printId: 'printId'
        }),
        ...mapState('permanent', {
            macAddress: 'macAddress',
            originBusinessObject: 'originBusinessObject',
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE',
            singleReflexWebSocket: 'singleReflexWebSocket'
        })
    },
    mounted () {
        this.setSteps(ID_NUMBER_CHANGE_YINCHUAN);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'setSteps', 'changeTipPage', 'setUserBusinessId', 'setCurrentBusinessHandle', 'setCurrentBusinessName', 'setSingleReflexHandlePhoto', 'setPrintInfo']),
        async checkQualifications () {
            const res = await checkQualifications({
                name: this.identifyCard.partyName,
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                this.queryIdPhoto = addPrefix(res.result.lsXp);
                this.selfCollectionAddress = res.result.sfzLzdz;
                this.machineAddress = res.result.sfzSldz;
                this.acceptanceNumber = res.result.slh;

                if (res.result && res.result.status) {
                    this.handleGoOn(res.result);
                } else {
                    this.handlePring(res.result);
                }
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        handleGoOn (result) {
            this.setUserBusinessId(result.userBusinessId);
            this.setCurrentBusinessHandle(result.businessTypeCode);
            this.setCurrentBusinessName(this.originBusinessObject.find((i) => { return i.businessCode === result.businessTypeCode; }).businessName);
            if (result.status === 9) {
                this.handlePring(result);
                this.jumpStep = 'handCollectFirst';
            } else if (result.status === 4 || result.status === 3) {
                this.jumpStep = 'photoTip';
            } else if (result.status === 2) {
                this.jumpStep = 'payQuery';
            } else if (result.status === 7) {
                this.jumpStep = 'excelProcess';
            }
        },
        handlePring (result) {
            this.originFingerInfo = JSON.parse(JSON.stringify((result)));
            this.leftFingerInfo = fingerTranslationYinchuan(result).leftFingerInfo;
            this.rightFingerInfo = fingerTranslationYinchuan(result).rightFingerInfo;
        },
        jumpProcess () {
            this.changeStep(this.jumpStep ? this.jumpStep : 'down');
        },
        async fingerUploadFirst (finger) {
            this.newLeftFingerInfo = finger;
            this.changeStep('down');
        },
        async fingerUploadSecond (finger) {
            this.newRightFingerInfo = finger;

            this.uploadFingerInfo = uploadFingerTranslationYinchuan({
                originFingerInfo: this.originFingerInfo,
                left: this.newLeftFingerInfo,
                right: this.newRightFingerInfo
            });
            const res = await submitFingerprintData({
                ...this.checkResultInfos,
                ...this.uploadFingerInfo
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        phoneUpload (phone) {
            this.phoneNumber = phone;
            this.changeStep('down');
        },
        async getAcceptanceNumber (wayOfReceiving, placeOfCollection) {
            this.placeOfCollection = placeOfCollection;
            this.wayOfReceiving = wayOfReceiving;
            if (wayOfReceiving === '0') { // 本人领取
                const res = await submissionAccreditation({
                    lzxxXm: this.identifyCard.partyName,
                    lzxxGmsfzhm: this.identifyCard.certNumber,
                    lzfsdm: this.placeOfCollection,
                    lxdh: this.phoneNumber
                });
                if (res && res.code === '0') {
                    this.acceptanceNumber = res.result;
                    this.changeStep('handCollectFirst');
                } else {
                    this.changeTipPage({ message: res.message, enableBack: true });
                }
            } else { // 他人领取
                this.changeStep('receivingPeople');
            }
        },
        async getOtherAcceptanceNumber (partyName, certNumber) {
            const res = await submissionAccreditation({
                lzxxXm: partyName,
                lzxxGmsfzhm: certNumber,
                lzfsdm: this.placeOfCollection,
                lxdh: this.phoneNumber
            });
            if (res && res.code === '0') {
                this.acceptanceNumber = res.result;
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async photoNext (chosePhoto) {
            const res = await handlePhotoRequest(chosePhoto, this.CONSTANT);
            if (res && res.code === 1) {
                this.setSingleReflexHandlePhoto(addPrefix(res.obj));
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async uploadPhotoResult () {
            const res = await singleReflexHandleSavePhoto({
                photoStr: cancelPrefix(this.singleReflexHandlePhoto)
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async intervalCheck () {
            this.changeStep('down');
            // const res = await checkIdPayStatus({ slh: this.acceptanceNumber });
            // if (res && res.code === '0') {
            //     if (res.result.zfzt === '1') {
            //         this.changeStep('down');
            //     } else if (res.result.zfzt === '2') {
            //         this.changeTipPage({ message: '您办理的业务正在退款！' });
            //     } else if (res.result.zfzt === '3') {
            //         this.changeTipPage({ message: '您办理的业务退款成功！' });
            //     }
            // }
        },
        // async uploadProcess () {
        //     const res = await uploadIdMakeInfo({});
        //     if (res && res.code === '0') {
        //         this.changeStep('down');
        //     } else if (res) {
        //         this.changeTipPage({ message: res.message });
        //     }
        // },
        async excelProcess () {
            const res = await produceModelReq({
                code: 'sfzhz', // 身份证回执
                body: {
                    ...this.identifyCard,
                    selfCollectionAddress: this.selfCollectionAddress,
                    acceptanceNumber: this.acceptanceNumber,
                    phoneNumber: this.phoneNumber,
                    machineAddress: this.machineAddress,
                    time: this.$moment().format('YYYY年MM月DD日'),
                    limitTime: this.$moment().add(19, 'days').format('YYYY年MM月DD日'),
                    url: urlGetQrEncode(this.CONSTANT.idCardProcessingPayUrl + this.acceptanceNumber)
                }
            });

            if (res && res.code === '0') {
                this.setPrintInfo(res.result);// 预览所需图片Id
                return await this.printProcess();
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
                return false;
            }
        },
        async printProcess () {
            const res = await toPrint({
                path: getPrintPrefix(this.printId)
            });
            if (res && res.code === 1) {
                console.log('身份证回执打印完成！');
            } else {
                this.changeTipPage({ message: '打印机缺纸，请联系工作人员。', enableBack: true });
            }
        },
        async resultProcess () {
            const res = await updateCardComplete({});
            if (res && res.code === '0') {
                this.result = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
                return false;
            }
        }
    }
};
</script>
