<template>
    <div>
        <div class="kd-page-title">选择方式</div>
        <div class="kd-page-content-wrapper wrap-info">
            <el-form class="uploadLabel">
                <el-form-item label="领取人：" label-width="300px">
                    <el-radio-group v-model="wayOfReceiving">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in wayOfReceivingArr"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="领取方式：" label-width="300px">
                    <el-radio-group v-model="placeOfCollection">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in placeOfCollectionArr"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item
                    label="领取地点："
                    v-if="placeOfCollection === '10'"
                    label-width="300px"
                >
                    <span>{{ selfCollectionAddress }}</span>
                </el-form-item>
                <el-form-item
                    label="扫码填写邮寄地址："
                    v-if="placeOfCollection === '30'"
                    label-width="300px"
                >
                    <img :src="imgSrc" width="300" alt="">
                </el-form-item>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
            <kd-button icon="kedaIcon-circleright" text="下一步" :button-handle="goNext" :but-disable="!placeOfCollection"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import ems from '@/assets/yinchuan/ems.png';
import audioReady from '@/assets/media/household/28.mp3';
import audioMail from '@/assets/media/household/29.mp3';
import audioSure from '@/assets/media/household/38.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IdCardProcessingHandleType',
    props: {
        selfCollectionAddress: { type: String, default: '' },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    data () {
        return {
            placeOfCollectionArr: [
                { type: '10', value: '受理机关领取' },
                { type: '30', value: '邮寄' }
            ],
            wayOfReceivingArr: [
                { type: '0', value: '本人领取' },
                { type: '1', value: '他人代领' }
            ],
            wayOfReceiving: '0',
            placeOfCollection: '10',
            imgSrc: ems
        };
    },
    watch: {
        placeOfCollection (newValue, oldVal) {
            if (!oldVal) {
                return;
            }
            this.audioReady.pause();
            if (newValue === '10') {
                this.audioMail.pause();
                this.audioSure.play();
            } else {
                this.audioSure.pause();
                this.audioMail.play();
            }
        }
    },
    mounted () {
        this.audioReady = playAudio(audioReady);
        this.audioMail = playAudio(audioMail);
        this.audioSure = playAudio(audioSure);
        this.audioReady.play();
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        }),
        ...mapState('idCardProcessing', {
            acceptanceNumber: 'acceptanceNumber',
            phoneNumber: 'phoneNumber'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('idCardProcessing', ['setAcceptanceNumber', 'setWayOfReceiving', 'setPlaceOfCollection']),
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            await this.nextFunction(this.wayOfReceiving, this.placeOfCollection, this.phoneNumber);
            // if (this.wayOfReceiving === '0') { // 本人领取
            //
            // } else { // 他人领取
            //     this.changeStep('receivingPeople');
            // }
        }
    }
};
</script>

<style lang="scss" scoped>
    .bg {
        width: 34.37vw;
        height: 26.56vh;
        border-radius: 0.5vh;
        background: url("../../../../../assets/images/cardno_bg.png");
        background-size: 100% 100%;
        margin: 0 auto;
    }

    .wrap-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 400px;
            height: 300px;
            background: #fff;
        }
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        padding-top: 3vw;

        ::v-deep .el-form-item__content {
            font-size: 24px;
            margin-right: 10vw;
            height: 6vh;
            line-height: 6vh;
        }

        .uploadLabel .uploadChoose {
            margin-bottom: 2vw;
            width: 18vw;
        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85BAF0;
                text-align: center;
                line-height: 13vw;
            }
        }

        .postName, .recipientsPhone {
            color: #606266;
            width: 100%;
            font-size: 2.4vh;
        }

        .mailingAddress {
            display: block;
            width: 100%;
            font-size: 2.4vh;
            color: #606266;
        }

        ::v-deep .mailWrap .el-form-item__label {
            font-size: 2.4vh;
            color: #fff;
            height: 60px;
            line-height: 60px;
        }

        ::v-deep .mailWrap .el-input__inner {
            height: 4vh;
            margin: 1vh 0;
        }

        ::v-deep .mailWrap .el-form-item__content span {
            font-size: 2.4vh;
            color: #fff;
            height: 6vh;
            line-height: 6vh;
        }
    }
</style>
