<template>
	<div>
		<div class="kd-page-title">进行支付</div>
		<div class="kd-page-content-wrapper kd-page-padding payResult">
			<p>请使用支付宝/微信扫码支付</p>
			<qriously
					class="payValue"
					:value="CONSTANT.idCardProcessingPayUrl + acceptanceNumber"
					:size="260"
			/>
			<p class="tip1">请及时扫码缴费，未支付或支付失败将导致无法办证。</p>
			<p class="tip1">(二维码请拍照保存，当晚0：00前再次扫码可以退款取消办理)</p>
			<p class="tip1">正在打印回执，请稍后......</p>
		</div>
		<div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
			<kd-button
					icon="kedaIcon-policework"
					text="返回首页"
					type-class="home"
			/>
			<kd-button v-if="repeatPrint" icon="kedaIcon-circleleft" text="重新打印" :button-handle="preStep"/>
			<kd-button icon="kedaIcon-duigou" text="支付完成" :button-handle="next"/>
		</div>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import audioObj from '@/assets/media/household/33.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IdCardProcessingPayQuery',
    props: {
        acceptanceNumber: { type: String, default: '' },
        nextFunction: {
            type: Function,
            default: function () {
                this.changeStep('down');
            }
        },
        returnPrint: { type: Function }
    },
    data () {
        return {
            countDown: null,
            repeatPrint: false
        };
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        }),
        ...mapState('idCardProcessing', {
            queryIdPhoto: 'queryIdPhoto',
            phoneNumber: 'phoneNumber',
            result: 'result',
            machineAddress: 'machineAddress'
        })
    },
    async mounted () {
        this.audioObj = playAudio(audioObj);
        this.audioObj.play();
        // this.startCheckPayStatus();
        if (this.returnPrint) {
            const res = await this.returnPrint();
            this.repeatPrint = !res;
        }
    },
    beforeDestroy () {
        this.audioObj.destroy();
        clearInterval(this.countDown);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setPrintInfo']),
        async preStep () { // 上一步
            if (this.returnPrint) {
                const res = await this.returnPrint();
                this.repeatPrint = !res;
            }
        },
        startCheckPayStatus () {
            this.countDown = setInterval(async () => {
                this.nextFunction();
            }, 3000);
        },
        next () {
            this.nextFunction();
        }
    }
};
</script>

<style lang="scss" scoped>
	.wrap-info {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.payResult {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		font-size: 26px;

		p {
			margin-bottom: 20px;

			span {
				font-size: 48px;
			}
		}

		.tip1 {
			color: red;
			font-size: 22px;
			margin-bottom: 10px;
		}

		.payValue {
			background: #fff;
			margin-bottom: 20px;
		}
	}

</style>
