<template>
    <div>
        <div class="kd-page-title">
            照片采集
        </div>
        <div class="kd-page-content-wrapper collect-complete-box">
            <img
                v-if="singleReflexHandlePhoto"
                :src="singleReflexHandlePhoto"
                alt=""
            >
            <img
                v-else
                src="../../../../../assets/images/default/cameraDefault.png"
                alt=""
            >
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="下一步"
                :button-handle="goNext"
            />
        </div>
    </div>
</template>
<script>
import { mapMutations, mapState } from 'vuex';

export default {
    name: 'IdCardProcessingCollectComplete',
    data () {
        return {
            previewPhoto: null,
            chosePhoto: false
        };
    },
    computed: {
        ...mapState('common', {
            singleReflexHandlePhoto: 'singleReflexHandlePhoto'
        })
    },
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        async goNext () {
            await this.nextFunction();
        }
    }
};
</script>

<style lang="scss" scoped>
    .collect-complete-box {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            max-width: 100%;
            max-height: 100%;
        }
    }
</style>
